import React, { Component, Fragment } from 'react';
import { withRouter, Link } from 'react-router-dom';
import { Card, Row, Table, Col, message } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { getEthnicity, deleteEthnicity } from 'services/ethnicity';
import routeConstant from 'routes/route/routeConstant';
import { ColEditDelete } from 'components/common';
import './style.scss';

class Ethnicity extends Component {
  constructor(props) {
    super(props);
    this.handleDelete = this.handleDelete.bind(this);
    this.state = {
      ethnicity: [],
      loading: true,
    };
    this.title = 'Danh mc dn tc';
    this.columns = [
      {
        title: 'M dn tc',
        dataIndex: 'id',
        key: 'id',
        // sorter: (a, b) => a.townId.length - b.townId.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Tn dn tc',
        dataIndex: 'label',
        key: 'label',
        // sorter: (a, b) => a.label.length - b.label.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) =>
          this.state.ethnicity.length >= 1 ? (
            <Fragment>
              <ColEditDelete
                linkEdit={{
                  to: {
                    pathname: routeConstant.DANH_MUC__TUYEN_SINH__DAN_TOC__NHAP_DU_LIEU,
                    search: `?id=${record.id}`,
                  },
                }}
                onConfirmDelete={() => this.handleDelete(record.id)}
              />
            </Fragment>
          ) : null,
      },
    ];
    // rowSelection object indicates the need for row selection
    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
    this.cssBetweenRow = { marginBottom: '8px' };
  }

  componentDidMount = async () => {
    this.getEthnicity();
  };

  onChange = (value, selectedOptions) => {
    console.log(value, selectedOptions);
  };

  getEthnicity = async () => {
    const res = await getEthnicity();
    this.setState({ ethnicity: res.data.items, loading: false });
  };

  handleDelete = id => {
    // this.setState({ loading: true });
    deleteEthnicity(id).then(res => {
      if (res.status === 200) {
        message.success('Xa dn tc thnh cng.');
        this.getEthnicity();
      } else {
        message.error(res.message);
      }
    });
  };

  render() {
    return (
      <Card title={this.title}>
        <Row>
          <Col span={24}>
            <Link to={routeConstant.DANH_MUC__TUYEN_SINH__DAN_TOC__NHAP_DU_LIEU}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              loading={this.state.loading}
              rowSelection={this.rowSelection}
              bordered
              dataSource={this.state.ethnicity}
              columns={this.columns}
              onRow={() => ({
                onDoubleClick: () => {},
              })}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}
export default withRouter(props => (
  <Page {...props}>
    <Ethnicity {...props} />
  </Page>
));



